
"use client";

import React, { useState, useEffect, createContext, useContext, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { 
  onAuthStateChanged, 
  signInWithEmailAndPassword, 
  createUserWithEmailAndPassword, 
  signOut,
  sendEmailVerification,
  updateProfile as firebaseUpdateProfile,
  sendPasswordResetEmail,
  confirmPasswordReset as firebaseConfirmPasswordReset, // For verifying code and resetting
  updatePassword as firebaseUpdatePassword,
  type User as FirebaseUserType
} from 'firebase/auth';
import { auth } from '@/lib/firebase'; // Firebase auth instance
import type { User, Role } from '@/types'; // Your application's User type
import { useToast } from '@/hooks/use-toast';
import { ROUTES, ROLES } from '@/lib/constants';

interface AuthContextType {
  user: User | null; // This will be our app's User type, mapped from FirebaseUser
  firebaseUser: FirebaseUserType | null; // Expose raw Firebase user if needed
  login: (email: string, passwordAttempt: string) => Promise<boolean>;
  register: (email: string, passwordAttempt: string) => Promise<boolean>;
  logout: () => Promise<void>;
  sendVerificationEmail: () => Promise<void>;
  updateUserProfile: (profileData: { displayName?: string | null; photoURL?: string | null; }) => Promise<boolean>;
  requestPasswordReset: (email: string) => Promise<boolean>;
  confirmPasswordReset: (oobCode: string, newPassword: string) => Promise<boolean>;
  changePassword: (newPassword: string) => Promise<boolean>;
  isLoading: boolean;
  updateSession: () => Promise<void>; // Function to manually refresh appUser
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const [firebaseUser, setFirebaseUser] = useState<FirebaseUserType | null>(null);
  const [appUser, setAppUser] = useState<User | null>(null); // Our application's user object
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  const { toast } = useToast();

  const mapFirebaseUserToAppUser = useCallback(async (fbUser: FirebaseUserType | null): Promise<User | null> => {
    if (!fbUser) return null;

    try {
      const response = await fetch(`/api/users/fb/${fbUser.uid}`); // Endpoint baru untuk get profile by Firebase UID
      if (response.ok) {
        const localProfileData: User = await response.json();
        return {
          id: fbUser.uid, // Gunakan Firebase UID sebagai ID utama di appUser
          email: fbUser.email || '',
          name: fbUser.displayName,
          avatarUrl: fbUser.photoURL,
          isVerified: fbUser.emailVerified,
          // Ambil data spesifik aplikasi dari profil lokal
          role: localProfileData.role, 
          fullName: localProfileData.fullName || fbUser.displayName || fbUser.email?.split('@')[0],
          phone: localProfileData.phone,
          address: localProfileData.address,
          birthDate: localProfileData.birthDate,
          bio: localProfileData.bio,
          nis: localProfileData.nis,
          nip: localProfileData.nip,
          joinDate: localProfileData.joinDate,
          kelas: localProfileData.kelas,
          mataPelajaran: localProfileData.mataPelajaran,
        };
      } else {
        // Jika profil lokal tidak ditemukan (misalnya, pengguna baru saja mendaftar Firebase
        // dan profil lokal belum dibuat atau ada error saat sinkronisasi),
        // kembalikan data dasar dari Firebase.
        console.warn(`Local profile for Firebase UID ${fbUser.uid} not found. API status: ${response.status}`);
        return {
          id: fbUser.uid,
          email: fbUser.email || '',
          name: fbUser.displayName,
          avatarUrl: fbUser.photoURL,
          isVerified: fbUser.emailVerified,
          role: 'siswa', // Default role jika profil lokal tidak ada
          fullName: fbUser.displayName || fbUser.email?.split('@')[0],
        };
      }
    } catch (error) {
        console.error("Error fetching local user profile:", error);
        // Fallback jika API error
        return {
          id: fbUser.uid,
          email: fbUser.email || '',
          name: fbUser.displayName,
          avatarUrl: fbUser.photoURL,
          isVerified: fbUser.emailVerified,
          role: 'siswa', // Default role
          fullName: fbUser.displayName || fbUser.email?.split('@')[0],
        };
    }
  }, []);

  const updateSession = useCallback(async () => {
    setIsLoading(true);
    const currentFbUser = auth.currentUser;
    if (currentFbUser) {
      await currentFbUser.reload(); // Reload Firebase user to get latest state (e.g., emailVerified)
      setFirebaseUser(currentFbUser);
      const newAppUser = await mapFirebaseUserToAppUser(currentFbUser);
      setAppUser(newAppUser);
    } else {
      setFirebaseUser(null);
      setAppUser(null);
    }
    setIsLoading(false);
  }, [mapFirebaseUserToAppUser]);


  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (fbUser) => {
      setIsLoading(true);
      if (fbUser) {
        await fbUser.reload(); // Selalu reload untuk mendapatkan status emailVerified terbaru
      }
      setFirebaseUser(fbUser);
      const currentAppUser = await mapFirebaseUserToAppUser(fbUser);
      setAppUser(currentAppUser);
      setIsLoading(false);
    });
    return () => unsubscribe();
  }, [mapFirebaseUserToAppUser]);

  const login = async (email: string, passwordAttempt: string): Promise<boolean> => {
    setIsLoading(true);
    try {
      await signInWithEmailAndPassword(auth, email, passwordAttempt);
      // onAuthStateChanged akan handle setting user dan mapping.
      // Jika mapFirebaseUserToAppUser tidak menemukan local profile, ia akan fallback.
      // Navigasi akan dihandle oleh halaman berdasarkan appUser.isVerified
      toast({ title: "Login Berhasil", description: "Selamat datang!" });
      setIsLoading(false);
      return true;
    } catch (error: any) {
      console.error("Firebase login error:", error);
      let errorMessage = "Email atau kata sandi salah.";
      if (error.code === 'auth/invalid-credential') {
        errorMessage = "Kredensial tidak valid. Periksa email dan kata sandi Anda.";
      } else if (error.code === 'auth/user-not-found') {
        errorMessage = "Pengguna dengan email ini tidak ditemukan.";
      } else if (error.code === 'auth/wrong-password') {
        errorMessage = "Kata sandi salah.";
      }
      toast({ title: "Login Gagal", description: errorMessage, variant: "destructive" });
      setIsLoading(false);
      return false;
    }
  };

  const register = async (email: string, passwordAttempt: string): Promise<boolean> => {
    setIsLoading(true);
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, passwordAttempt);
      const fbUser = userCredential.user;

      // Setelah pengguna Firebase dibuat, buat profil lokal di database Anda
      const localProfilePayload = {
        firebaseUid: fbUser.uid,
        email: fbUser.email,
        fullName: fbUser.email?.split('@')[0] || "Pengguna Baru",
        role: 'siswa' as Role, // Default role untuk registrasi baru
        isVerified: false, // Email belum diverifikasi di Firebase saat ini
        name: fbUser.email?.split('@')[0], // Default name
      };

      const response = await fetch('/api/users', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(localProfilePayload),
      });

      if (!response.ok) {
        const errorData = await response.json();
        // Jika pembuatan profil lokal gagal, idealnya pengguna Firebase yang baru dibuat juga dihapus (rollback)
        // Untuk saat ini, kita hanya log dan toast error
        console.error("Gagal membuat profil lokal setelah registrasi Firebase:", errorData.message || "Unknown error");
        toast({ title: "Pendaftaran Gagal", description: `Gagal menyimpan profil lokal: ${errorData.message || "Unknown error"}`, variant: "destructive" });
        // Hapus pengguna Firebase jika profil lokal gagal dibuat? (Opsional, butuh hak admin)
        // await fbUser.delete();
        setIsLoading(false);
        return false;
      }
      
      // Lanjutkan dengan mengirim email verifikasi Firebase
      await sendEmailVerification(fbUser);
      
      toast({ title: "Pendaftaran Berhasil", description: "Silakan cek email Anda untuk verifikasi." });
      // onAuthStateChanged akan memuat appUser baru dengan data dari profil lokal (jika sukses)
      // dan halaman akan mengarahkan ke /verify-email
      setIsLoading(false);
      return true;
    } catch (error: any) {
      console.error("Firebase registration error:", error);
      let errorMessage = "Terjadi kesalahan saat pendaftaran.";
      if (error.code === 'auth/email-already-in-use') {
        errorMessage = "Alamat email ini sudah terdaftar.";
      }
      toast({ title: "Pendaftaran Gagal", description: errorMessage, variant: "destructive" });
      setIsLoading(false);
      return false;
    }
  };

  const logout = async () => {
    setIsLoading(true);
    try {
      await signOut(auth);
      setAppUser(null);
      setFirebaseUser(null);
      router.push(ROUTES.LOGIN);
      toast({ title: "Berhasil Keluar" });
    } catch (error: any) {
      console.error("Firebase logout error:", error);
      toast({ title: "Logout Gagal", description: error.message, variant: "destructive" });
    } finally {
        setIsLoading(false);
    }
  };

  const sendVerificationEmail = async () => {
    if (firebaseUser) {
      setIsLoading(true);
      try {
        await sendEmailVerification(firebaseUser);
        toast({ title: "Email Verifikasi Terkirim", description: "Silakan cek kotak masuk Anda." });
      } catch (error: any) {
        toast({ title: "Gagal Mengirim Email", description: error.message, variant: "destructive" });
      } finally {
        setIsLoading(false);
      }
    } else {
      toast({ title: "Error", description: "Tidak ada pengguna untuk mengirim email verifikasi.", variant: "destructive" });
    }
  };

  const updateUserProfile = async (profileData: { displayName?: string | null; photoURL?: string | null; }): Promise<boolean> => {
    if (firebaseUser) {
      setIsLoading(true);
      try {
        await firebaseUpdateProfile(firebaseUser, profileData);
        await updateSession(); // Panggil updateSession untuk me-refresh appUser
        toast({ title: "Profil Firebase Diperbarui" });
        setIsLoading(false);
        return true;
      } catch (error: any) {
        toast({ title: "Gagal Memperbarui Profil Firebase", description: error.message, variant: "destructive" });
        setIsLoading(false);
        return false;
      }
    }
    return false;
  };
  
  const requestPasswordReset = async (email: string): Promise<boolean> => {
    setIsLoading(true);
    try {
      await sendPasswordResetEmail(auth, email);
      toast({ title: "Email Reset Terkirim", description: "Jika email terdaftar, instruksi akan dikirim." });
      setIsLoading(false);
      return true;
    } catch (error: any) {
      toast({ title: "Gagal Meminta Reset", description: error.message, variant: "destructive" });
      setIsLoading(false);
      return false;
    }
  };

  const confirmPasswordReset = async (oobCode: string, newPassword: string): Promise<boolean> => {
    setIsLoading(true);
    try {
      await firebaseConfirmPasswordReset(auth, oobCode, newPassword);
      toast({ title: "Kata Sandi Direset", description: "Anda sekarang dapat login dengan kata sandi baru." });
      router.push(ROUTES.LOGIN);
      setIsLoading(false);
      return true;
    } catch (error: any) {
      toast({ title: "Gagal Reset Kata Sandi", description: error.message, variant: "destructive" });
      setIsLoading(false);
      return false;
    }
  };
  
  const changePassword = async (newPassword: string): Promise<boolean> => {
    if (firebaseUser) {
      setIsLoading(true);
      try {
        await firebaseUpdatePassword(firebaseUser, newPassword);
        toast({ title: "Kata Sandi Diubah", description: "Kata sandi Anda telah berhasil diperbarui." });
        setIsLoading(false);
        return true;
      } catch (error: any)
       {
        if (error.code === 'auth/requires-recent-login') {
            toast({ title: "Autentikasi Ulang Diperlukan", description: "Silakan login kembali sebelum mengubah kata sandi.", variant: "destructive" });
            await logout(); 
        } else {
            toast({ title: "Gagal Mengubah Kata Sandi", description: error.message, variant: "destructive" });
        }
        setIsLoading(false);
        return false;
      }
    }
    return false;
  };

  return (
    <AuthContext.Provider value={{ 
      user: appUser, 
      firebaseUser,
      login, 
      register, 
      logout, 
      sendVerificationEmail,
      updateUserProfile,
      requestPasswordReset,
      confirmPasswordReset,
      changePassword,
      isLoading,
      updateSession,
    }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth harus digunakan di dalam AuthProvider');
  }
  return context;
};
